		</div>
		<div class="col-12 pl-3 pr-0 pb-0 main_box_heigth " v-if="selectReport.report.rounds[step][—Åname].squads_before.length>0">
			<div class="row " >
        <!-- {{selectReport.report.rounds[step][—Åname].squads_before[0].owner}} -->

        <div @click="selectUser=index" :class="'col-3 px-0  user ' + (selectUser==index?'select':'')" v-for="(floot, index) in selectReport.report.rounds[step][—Åname].squads_before"  :key="index">
          <p v-if="(floot.type=='fleet')&&(—Åname=='attacker')" class=" text-center ">{{floot.id}}</p>
          <p v-if="(floot.type=='fleet')&&(—Åname=='defender')" class=" text-center ">{{floot.owner}}</p>
          <p v-if="(floot.type=='defence')" class=" text-center ">–ü–ª–∞–Ω–µ—Ç–∞</p>
					<div class="d-flex px-0 ">
						<div class="col gr text-center px-0" title="–û—Ä—É–∂–∏–µ">{{floot.attack_level.toLocaleString('ru-RU')}}</div>
						<div class="col ye text-center px-0" title="–ë—Ä–æ–Ω—è">{{floot.armor_level.toLocaleString('ru-RU')}}</div>
						<div class="col rd text-center px-0" title="–©–∏—Ç—ã">{{floot.shield_level.toLocaleString('ru-RU')}}</div>
					</div>
					<HP class="mx-auto" height="1" width="95%" :fill="checkFlootHp(selectReport.report.rounds[step][—Åname].squads_after[index])" />
				</div>
			</div>

			<!-- <div class="d-flex  floot" >
				<div class="mr-2 minfont pt-1 ">–§–ª–æ—Ç—ã:</div> -->
				<!-- <div :class="'floot_one minfont pt-1 mx-2 ' + ( index==0?'select':'')" v-for="(unit, index) in selectReport.report.rounds[step][—Åname].squads_before[selectUser].units"  :key="index">12321</div> -->
				<!-- <div class="floot_one minfont pt-1 mx-2 ">alpha</div>
			</div> -->
		</div>
	</div>

  <div class="row" v-if="selectReport.report.rounds[step][—Åname].squads_before.length<1">
    <div class="col-12 text-center mt-5">
      –ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö
    </div>
  </div>

	<div class="row pb-2" v-if="selectReport.report.rounds[step][—Åname].squads_before.length>0">

		<div class="col-6 mt-2" v-for="unit in selectReport.report.rounds[step][—Åname].squads_before[selectUser].units"  :key="unit.type+unit.speed+unit.attack">
			<a href="#"><b>{{namesUnit[unit.type]}}</b> ({{unit.amount}})</a>
			<HP height="1" width="100px" :fill="chekHp(unit.type)"  />
