			–§–ª–æ—Ç —É–Ω–∏—á—Ç–æ–∂–µ–Ω
		</p>
	</div>

	<div class="row mt-auto bot_line">
		<div class="col-6 py-1 main_box_1 d-flex justify-content-between">
