<script>
import HP from '@/components/report/compon/HpComponent.vue'
export default {
	created(){
		this.checkFirstHp()
	},
	mounted(){
		this.heighted = document.querySelector('.main_box').offsetHeight
	},
	props: {
		selectReport: Object,
    —Åname: String,
    step: Number,
	},
  components:{
    HP,
  },
	data() {
		return {
			countUnit: {},
			heighted: '',
      names: {attacker:"–ê—Ç–∞–∫—É—é—â–∏–∏–µ —Ñ–ª–æ—Ç—ã", defender:"–û–±–æ—Ä–æ–Ω—è—é—â–∏–µ—Å—è —Ñ–ª–æ—Ç—ã", draw: "–ù–∏—á—å—è",},
      namesUnit: {shuttle:"–®–∞—Ç—Ç–ª",fighter:"–ò—Å—Ç—Ä–µ–±–∏—Ç–µ–ª—å",transport: "–¢—Ä–∞–Ω—Å–ø–æ—Ä—Ç",energodrone:"–≠–Ω–µ—Ä–≥–æ–¥—Ä–æ–Ω",attacker:"–®—Ç—É—Ä–º–æ–≤–∏–∫",frigate:"–§—Ä–µ–≥–∞—Ç",galaction:"–ì–∞–ª–∞–∫—Ç–∏–æ–Ω",destroyer:"–†–∞–∑—Ä—É—à–∏—Ç–µ–ª—å",bombardier:"–ë–æ–º–±–∞—Ä–¥–∏—Ä",colossus:"–ö–æ–ª–æ—Å—Å",scout:"–†–∞–∑–≤–µ–¥–∑–æ–Ω–¥",collector:"–ö–æ–ª–ª–µ–∫—Ç–æ—Ä",pioneer:"–ü–µ—Ä–≤–æ–ø—Ä–æ—Ö–æ–¥–µ—Ü",corvette:"–ö–æ—Ä–≤–µ—Ç",big_energy_shield:"–ë–æ–ª—å—à–æ–π —ç–Ω–µ—Ä–≥–æ–∫—É–ø–æ–ª",small_energy_shield:"–ú–∞–ª—ã–π —ç–Ω–µ—Ä–≥–æ–∫—É–ø–æ–ª",lepton_cannon:"–õ–µ–ø—Ç–æ–Ω–Ω–∞—è –ø—É—à–∫–∞",photon_cannon:"–§–æ—Ç–æ–Ω–Ω–∞—è –ø—É—à–∫–∞",graviton_weapon:"–ì—Ä–∞–≤–∏—Ç–æ–Ω–Ω–æ–µ –æ—Ä—É–¥–∏–µ",ultraviolet_laser:"–£–ª—å—Ç—Ä–∞—Ñ–∏–æ–ª–µ—Ç–æ–≤—ã–π –ª–∞–∑–µ—Ä",infrared_laser:"–ò–Ω—Ñ—Ä–∞–∫—Ä–∞—Å–Ω—ã–π –ª–∞–∑–µ—Ä",missile_block:"–†–∞–∫–µ—Ç–Ω—ã–π –±–ª–æ–∫"},

      selectFloot:{attacker:0, defender:0},
      selectUser: 0,
			idTime: '',
		}
	},

	computed:{
		fullHp(){      // —Å—á–∏—Ç–∞–µ—Ç –ø–æ–ª–Ω—ã–π —Ö–ø –æ–±–æ—Ä–æ–Ω—è—é—â–µ–≥–æ—Å—è –∏ –∞—Ç–∞–∫—É—é—â–µ–≥–æ
			let armor = 0, shield = 0, countUnit = 0
			this.selectReport.report.rounds[this.step][this.—Åname].squads_after.forEach(user => {
				countUnit += this.countUnit[user.type+user.id]
				user.units.forEach((unit) => {
					armor = armor + unit.armor
					shield = shield + unit.shield
				})
			})
			let fulHp = (shield+armor)/(2*countUnit)
			return String(fulHp)
		}
	},
	methods:{
		checkFlootHp(user){     // —Ä–∞—Å—á–∏—Ç—ã–≤–∞–µ—Ç hp —É –æ—Ç–¥–µ–ª—å–Ω—ã—Ö —Ñ–ª–æ—Ç–æ–≤
			let SumArmor = 0
				user.units.forEach((unit) => {
					SumArmor += unit.armor
					SumArmor += unit.shield
				})
			let fulHp = (SumArmor)/(2*this.countUnit[user.type+user.id])

			return String(fulHp)
		},
		checkFirstHp(){  //–∑–∞–ø–æ–º–∏–Ω–∞–µ—Ç –Ω–∞—á–∞–ª—å–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∫–æ—Ä–∞–±–ª–µ–π
			this.selectReport.report.rounds[0][this.—Åname].squads_before.forEach(user => {
				this.countUnit[user.type+user.id] = user.units.length
			})
		},
		checkDie(type){
			let ans = this.selectReport.report.rounds[this.step][this.—Åname].squads_after[this.selectUser].units.find(item => item.type == type)
			return ans
		},
		chekHp( type){    //—Ä–∞—Å—á–∏—Ç—ã–≤–∞–µ—Ç hp –æ—Ç–¥–µ–ª—å–Ω—ã—Ö –∫–æ—Ä–∞–±–ª–µ–π
			let unit = this.checkDie(type)
			if (unit == undefined) {
				return String(0)
			}
			return String((unit.shield+unit.armor)/2)
		},
	},

}
</script>

